import React, { useContext } from "react";
import { VehicleContext } from "../VehiclePolicy";
import {
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Box,
  Typography,
} from "@mui/material";

const rateOption_ = [
  { Account: "0.00" },
  { Account: "50,000.00" },
  { Account: "75,000.00" },
  { Account: "100,000.00" },
  { Account: "150,000.00" },
  { Account: "200,000.00" },
  { Account: "250,000.00" },
  { Account: "300,000.00" },
];
const compreTypeOption_ = [
  { Account: "" },
  { Account: "PRIVATE VEHICLE" },
  { Account: "LIGHT AND MEDIUM VEHICLE" },
  { Account: "HEAVY VEHICLE" },
  { Account: "MC/TC-MOTORCYCLE/TRICYCLE" },
];

const tplTypeOption_ = [
  { Account: "", PremuimPaid: "0.00" },
  { Account: "LIGHT PRIVATE VEHICLE(1YR)", PremuimPaid: "447.01" },
  { Account: "MEDIUM PRIVATE VEHICLE(1YR)", PremuimPaid: "486.92" },
  { Account: "HEAVY PRIVATE VEHICLE(1YR)", PremuimPaid: "957.88" },
  {
    Account: "MC/TC - MOTORCYCLE/TRICYCLE(1YR)",
    PremuimPaid: "199.55",
  },
  { Account: "LIGHT PRIVATE VEHICLE(3YR)", PremuimPaid: "1285.14" },
  {
    Account: "MEDIUM PRIVATE VEHICLE(3YR)",
    PremuimPaid: "1396.89",
  },
  { Account: "HEAVY PRIVATE VEHICLE(3YR)", PremuimPaid: "2746.34" },
  {
    Account: "MC/TC - MOTORCYCLE/TRICYCLE(3YR)",
    PremuimPaid: "574.72",
  },
];

export default function PolicyTypeDetails() {
  const {
    state,
    handleInputChange,
    handleDateChange,
    showField,
    data,
    isAddOrEditMode,
  } = useContext(VehicleContext);
  return (
    <div>
      {state.form_type.toLowerCase() === "tpl" && (
        <React.Fragment>
          <Typography variant="h6" sx={{ marginY: "10px" }}>
            THIRD PARTY LIABILITY
          </Typography>
          <fieldset
            style={{
              display: "flex",
              gap: "10px",
              gridArea: "content4",
              padding: "15px",
              border: "1px solid #cbd5e1",
              borderRadius: "5px",
              flex: 1,
            }}
          >
            <legend>Section I/II</legend>
            <FormControl
              size="small"
              fullWidth
              disabled={!showField.thirdparty || isAddOrEditMode}
            >
              <InputLabel id="Type">Type</InputLabel>
              <Select
                labelId="Type"
                value={state.TplType}
                label="Type"
                name="TplType"
                onChange={(e) => {
                  const selected: any = tplTypeOption_.filter(
                    (items) => items.Account === e.target.value
                  )[0];
                  console.log(selected);
                  handleInputChange(e);
                  handleDateChange(selected.PremuimPaid, "PremiumPaid");
                }}
              >
                {tplTypeOption_.map((items: any, idx: number) => {
                  return (
                    <MenuItem key={idx} value={items.Account}>
                      {items.Account}
                    </MenuItem>
                  );
                })}
              </Select>
            </FormControl>
            <TextField
              disabled={isAddOrEditMode}
              fullWidth
              variant="outlined"
              size="small"
              label="Premium Paid"
              name="PremiumPaid"
              value={state.PremiumPaid}
              onChange={(e) => {
                handleInputChange(e);
              }}
            />
          </fieldset>
        </React.Fragment>
      )}
      {state.form_type.toLowerCase() === "com" && (
        <React.Fragment>
          <Typography variant="h6" sx={{ marginY: "10px" }}>
            COMPREHENSEVE
          </Typography>
          <fieldset
            style={{
              display: "flex",
              flexDirection: "column",
              gap: "10px",
              gridArea: "content4",
              padding: "15px",
              border: "1px solid #cbd5e1",
              borderRadius: "5px",
              flex: 1,
            }}
          >
            <legend>Section III/IV</legend>
            <Box
              sx={(theme) => ({
                display: "flex",
                columnGap: "15px",
                flexDirection: "row",
                [theme.breakpoints.down("md")]: {
                  flexDirection: "column",
                  rowGap: "10px",
                },
              })}
            >
              <TextField
                disabled={isAddOrEditMode}
                fullWidth
                variant="outlined"
                size="small"
                label="Estimated Value of Schedule Vehicle"
                name="EVSV"
                value={state.EVSV}
                onChange={handleInputChange}
              />
              <TextField
                disabled={isAddOrEditMode}
                variant="outlined"
                size="small"
                label="Aircon"
                fullWidth
                name="Aircon"
                value={state.Aircon}
                onChange={handleInputChange}
              />
              <TextField
                disabled={isAddOrEditMode}
                variant="outlined"
                size="small"
                label="Stereo"
                fullWidth
                name="Stereo"
                value={state.Stereo}
                onChange={handleInputChange}
              />
              <TextField
                disabled={isAddOrEditMode}
                variant="outlined"
                size="small"
                label="Magwheels"
                fullWidth
                name="Magwheels"
                value={state.Magwheels}
                onChange={handleInputChange}
              />
            </Box>
            <Box
              sx={(theme) => ({
                display: "flex",
                columnGap: "15px",
                flexDirection: "row",
                [theme.breakpoints.down("md")]: {
                  flexDirection: "column",
                  rowGap: "10px",
                },
              })}
            >
              <div
                style={{
                  display: "flex",
                  gap: "10px",
                  flex: 1,
                }}
              >
                <TextField
                  disabled={isAddOrEditMode}
                  variant="outlined"
                  size="small"
                  label="Others(Specify)"
                  fullWidth
                  name="OthersDesc"
                  value={state.OthersDesc}
                  onChange={handleInputChange}
                />
                <p
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    width: "30px",
                    height: "3px",
                    backgroundColor: "black",
                  }}
                ></p>
                <TextField
                  disabled={isAddOrEditMode}
                  variant="outlined"
                  size="small"
                  name="OthersRate"
                  value={state.OthersRate}
                  onChange={handleInputChange}
                />
              </div>
              <FormControl
                disabled={isAddOrEditMode}
                size="small"
                sx={{ flex: 1 }}
              >
                <InputLabel id="Type">Type</InputLabel>
                <Select
                  labelId="Type"
                  value={state.CompreType}
                  label="Type"
                  name="CompreType"
                  onChange={handleInputChange}
                >
                  {compreTypeOption_.map((items: any, idx: number) => {
                    return (
                      <MenuItem key={idx} value={items.Account}>
                        {items.Account}
                      </MenuItem>
                    );
                  })}
                </Select>
              </FormControl>
            </Box>

            <div style={{ display: "flex", columnGap: "10px" }}>
              <fieldset
                style={{
                  display: "flex",
                  flexDirection: "column",
                  gap: "10px",
                  gridArea: "content4",
                  padding: "15px",
                  border: "1px solid #cbd5e1",
                  borderRadius: "5px",
                  flex: 1,
                }}
              >
                <TextField
                  disabled={isAddOrEditMode}
                  variant="outlined"
                  size="small"
                  label="Deductible"
                  fullWidth
                  name="Deductible"
                  value={state.Deductible}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  variant="outlined"
                  size="small"
                  label="Towing"
                  fullWidth
                  name="Towing"
                  value={state.Towing}
                  onChange={handleInputChange}
                />
                <TextField
                  disabled={isAddOrEditMode}
                  variant="outlined"
                  size="small"
                  label="Authorized Repair Limit"
                  fullWidth
                  name="ARL"
                  value={state.ARL}
                  onChange={handleInputChange}
                />
              </fieldset>
              <fieldset
                style={{
                  display: "flex",
                  flexDirection: "column",
                  gap: "10px",
                  gridArea: "content4",
                  padding: "15px",
                  border: "1px solid #cbd5e1",
                  borderRadius: "5px",
                  flex: 1,
                }}
              >
                <FormControl size="small" fullWidth disabled={isAddOrEditMode}>
                  <InputLabel id="BodyInjury">Bodily Injury</InputLabel>
                  <Select
                    labelId="BodyInjury"
                    value={state.BodyInjury}
                    label="BodyInjury"
                    name="BodyInjury"
                    onChange={handleInputChange}
                  >
                    {rateOption_.map((items: any, idx: number) => {
                      return (
                        <MenuItem key={idx} value={items.Account}>
                          {items.Account}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
                <FormControl disabled={isAddOrEditMode} fullWidth size="small">
                  <InputLabel id="PropertyDamage">Property Damage</InputLabel>
                  <Select
                    labelId="PropertyDamage"
                    value={state.PropertyDamage}
                    label="PropertyDamage"
                    name="PropertyDamage"
                    onChange={handleInputChange}
                  >
                    {rateOption_.map((items: any, idx: number) => {
                      return (
                        <MenuItem key={idx} value={items.Account}>
                          {items.Account}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
                <FormControl disabled={isAddOrEditMode} fullWidth size="small">
                  <InputLabel id="PersinalAccident">
                    Personal Accident
                  </InputLabel>
                  <Select
                    labelId="PersinalAccident"
                    value={state.PersinalAccident}
                    label="PersinalAccident"
                    name="PersinalAccident"
                    onChange={handleInputChange}
                  >
                    {rateOption_.map((items: any, idx: number) => {
                      return (
                        <MenuItem key={idx} value={items.Account}>
                          {items.Account}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
              </fieldset>
            </div>
          </fieldset>
        </React.Fragment>
      )}
      <br />
      <FormControl
        disabled={isAddOrEditMode}
        fullWidth
        size="small"
        sx={(theme) => ({
          width: "300px",
          [theme.breakpoints.down("md")]: {
            width: "100%",
          },
        })}
      >
        <InputLabel id="Denomination">Domination</InputLabel>
        <Select
          labelId="Denomination"
          value={state.Denomination}
          label="Denomination"
          name="Denomination"
          onChange={handleInputChange}
        >
          {[
            { type: "" },
            ...data.data.vehiclePolicy.rate[`${state.form_type.toLowerCase()}`],
          ].map((items: any, idx: number) => {
            return (
              <MenuItem key={idx} value={items.type}>
                {items.type}
              </MenuItem>
            );
          })}
        </Select>
      </FormControl>
    </div>
  );
}
